<template>
  <div class="popup-container">
    <div class="header">
      <h3>üîß Google Ads Êï∞ÊçÆÊã¶Êà™Âô®</h3>
      <p class="subtitle">ÈÄöËøáÁΩëÁªúÊã¶Êà™‰øÆÊîπAPIÂìçÂ∫îÊï∞ÊçÆ</p>
    </div>
    
    <div class="content">
      <div class="status" :class="statusClass">
        <div class="status-icon">{{ statusIcon }}</div>
        <div class="status-text">{{ statusMessage }}</div>
      </div>

      <div class="info-card" v-if="isGoogleAdsPage">
        <div class="info-item">
          <span class="label">Êã¶Êà™Áä∂ÊÄÅ:</span>
          <span class="value" :class="{ 'active': interceptorActive }">
            {{ interceptorActive ? 'Â∑≤ÊøÄÊ¥ª' : 'Êú™ÊøÄÊ¥ª' }}
          </span>
        </div>
        <div class="info-item">
          <span class="label">ÈÖçÁΩÆËßÑÂàô:</span>
          <span class="value">{{ configRulesCount }} Êù°</span>
        </div>
      </div>

      <div class="actions">
        <button 
          @click="activateInterceptor" 
          :disabled="isProcessing || !isGoogleAdsPage"
          class="btn-primary"
        >
          {{ isProcessing ? 'ÊøÄÊ¥ª‰∏≠...' : 'ÊøÄÊ¥ªÊã¶Êà™Âô®' }}
        </button>
        
        <button 
          @click="refreshPage" 
          :disabled="!isGoogleAdsPage"
          class="btn-secondary"
        >
          Âà∑Êñ∞È°µÈù¢
        </button>
        
        <button 
          @click="showConfig" 
          class="btn-secondary"
        >
          {{ showConfigData ? 'ÈöêËóèÈÖçÁΩÆ' : 'Êü•ÁúãÈÖçÁΩÆ' }}
        </button>
      </div>

      <div v-if="showConfigData" class="config-preview">
        <h4>ÂΩìÂâçÈÖçÁΩÆ:</h4>
        <div class="config-summary">
          <p><strong>ÂπøÂëäÁªÑËßÑÂàô:</strong> {{ config.adGroups?.length || 0 }} Êù°</p>
          <p><strong>ÂÖ®Â±ÄÊï∞ÊçÆ:</strong> {{ config.settings?.enableGlobalData ? 'Â∑≤ÂêØÁî®' : 'Êú™ÂêØÁî®' }}</p>
          <p><strong>ËØ¶ÁªÜÊó•Âøó:</strong> {{ config.settings?.verbose ? 'Â∑≤ÂêØÁî®' : 'Êú™ÂêØÁî®' }}</p>
        </div>
        <pre>{{ JSON.stringify(config, null, 2) }}</pre>
      </div>

      <div class="help-section">
        <h4>üìñ ‰ΩøÁî®ËØ¥Êòé</h4>
        <ol>
          <li>ÊâìÂºÄ Google Ads È°µÈù¢</li>
          <li>ÁÇπÂáª"ÊøÄÊ¥ªÊã¶Êà™Âô®"ÊåâÈíÆ</li>
          <li>Âà∑Êñ∞È°µÈù¢‰ª•ÂÆåÂÖ®Â∫îÁî®Êã¶Êà™</li>
          <li>Êü•Áúã‰øÆÊîπÂêéÁöÑÊï∞ÊçÆ</li>
        </ol>
        <p class="note">üí° Êã¶Êà™Âô®‰ºö‰øÆÊîπ API ÂìçÂ∫îÊï∞ÊçÆÔºåÊØî DOM ‰øÆÊîπÊõ¥ÂΩªÂ∫ï</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GoogleAdsInterceptorPopup',
  data() {
    return {
      statusMessage: 'Ê£ÄÊµã‰∏≠...',
      statusClass: 'loading',
      statusIcon: '‚è≥',
      isProcessing: false,
      isGoogleAdsPage: false,
      interceptorActive: false,
      showConfigData: false,
      config: {},
      configRulesCount: 0
    }
  },
  async mounted() {
    await this.loadConfig()
    await this.checkCurrentTab()
  },
  methods: {
    async loadConfig() {
      try {
        const result = await chrome.storage.local.get(['adsConfig'])
        this.config = result.adsConfig || {}
        this.configRulesCount = this.config.adGroups?.length || 0
      } catch (error) {
        console.error('Âä†ËΩΩÈÖçÁΩÆÂ§±Ë¥•:', error)
      }
    },
    
    async checkCurrentTab() {
      try {
        const tabs = await chrome.tabs.query({ active: true, currentWindow: true })
        const tab = tabs[0]
        
        if (!tab || !tab.url) {
          this.statusMessage = 'Êó†Ê≥ïÊ£ÄÊµãÂΩìÂâçÈ°µÈù¢'
          this.statusClass = 'error'
          this.statusIcon = '‚ùå'
          return
        }

        if (tab.url.includes('ads.google.com')) {
          this.isGoogleAdsPage = true
          
          // Ê£ÄÊü•Êã¶Êà™Âô®Áä∂ÊÄÅ
          try {
            const response = await chrome.tabs.sendMessage(tab.id, { action: 'getStatus' })
            if (response && response.success) {
              this.interceptorActive = response.status.isInjected
              this.statusMessage = this.interceptorActive ? 
                'Êã¶Êà™Âô®Â∑≤ÊøÄÊ¥ª' : 'ÂáÜÂ§áÂ∞±Áª™ÔºåÁÇπÂáªÊøÄÊ¥ª'
              this.statusClass = this.interceptorActive ? 'success' : 'ready'
              this.statusIcon = this.interceptorActive ? '‚úÖ' : 'üéØ'
            } else {
              this.statusMessage = 'ÂáÜÂ§áÂ∞±Áª™'
              this.statusClass = 'ready'
              this.statusIcon = 'üéØ'
            }
          } catch (error) {
            // content script ÂèØËÉΩËøòÊú™Âä†ËΩΩ
            this.statusMessage = 'ÂáÜÂ§áÂ∞±Áª™ÔºàËØ∑Âà∑Êñ∞È°µÈù¢Ôºâ'
            this.statusClass = 'warning'
            this.statusIcon = '‚ö†Ô∏è'
          }
        } else {
          this.statusMessage = 'ËØ∑ÊâìÂºÄ Google Ads È°µÈù¢'
          this.statusClass = 'warning'
          this.statusIcon = '‚ö†Ô∏è'
        }
      } catch (error) {
        this.statusMessage = 'Ê£ÄÊµãÂ§±Ë¥•'
        this.statusClass = 'error'
        this.statusIcon = '‚ùå'
      }
    },
    
    async activateInterceptor() {
      this.isProcessing = true
      this.statusMessage = 'Ê≠£Âú®ÊøÄÊ¥ªÊã¶Êà™Âô®...'
      this.statusClass = 'processing'
      this.statusIcon = '‚è≥'
      
      try {
        const tabs = await chrome.tabs.query({ active: true, currentWindow: true })
        const tab = tabs[0]
        
        if (!tab) {
          throw new Error('Êó†Ê≥ïËé∑ÂèñÂΩìÂâçÊ†áÁ≠æÈ°µ')
        }
        
        const response = await chrome.tabs.sendMessage(tab.id, { 
          action: 'startModification' 
        })
        
        if (response && response.success) {
          this.statusMessage = 'Êã¶Êà™Âô®Â∑≤ÊøÄÊ¥ªÔºÅÂª∫ËÆÆÂà∑Êñ∞È°µÈù¢'
          this.statusClass = 'success'
          this.statusIcon = '‚úÖ'
          this.interceptorActive = true
        } else {
          throw new Error(response?.error || 'ÊøÄÊ¥ªÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('ÊøÄÊ¥ªÂ§±Ë¥•:', error)
        if (error.message.includes('Could not establish connection')) {
          this.statusMessage = 'ËøûÊé•Â§±Ë¥•ÔºöËØ∑Âà∑Êñ∞È°µÈù¢ÂêéÈáçËØï'
          this.statusIcon = 'üîÑ'
        } else {
          this.statusMessage = 'ÊøÄÊ¥ªÂ§±Ë¥•: ' + error.message
          this.statusIcon = '‚ùå'
        }
        this.statusClass = 'error'
      } finally {
        this.isProcessing = false
      }
    },
    
    async refreshPage() {
      try {
        const tabs = await chrome.tabs.query({ active: true, currentWindow: true })
        const tab = tabs[0]
        if (tab) {
          await chrome.tabs.reload(tab.id)
          this.statusMessage = 'È°µÈù¢Âà∑Êñ∞‰∏≠...'
          this.statusClass = 'processing'
          this.statusIcon = 'üîÑ'
          
          // 2ÁßíÂêéÂÖ≥Èó≠popup
          setTimeout(() => {
            window.close()
          }, 2000)
        }
      } catch (error) {
        console.error('Âà∑Êñ∞È°µÈù¢Â§±Ë¥•:', error)
      }
    },
    
    showConfig() {
      this.showConfigData = !this.showConfigData
    }
  }
}
</script>

